import api from "@/api";
export default {
    namespaced: true,
    state() {
        return {
            allCity: [],
            isGet: false,
            selProvince: "", // 选中的省份
            selCity : {},
            hotCitys : [], // 热门城市
            reqCitys : [], // 最近搜索的城市
            localCity : {} // 当前访问的城市
        }
    },
    mutations: {
        setStatus(state, payload) {
            state.isGet = payload;
        },
        setAllCity(state, payload) {
            state.allCity = payload;
        },
        setProvince(state, payload) {
            state.selProvince = payload;
        },
        setCity(state, payload) {
            state.selCity = payload;
        },
        setHotCitys (state,payload) {
            state.hotCitys = payload;
        },
        setReqCitys (state,payload) {
            state.reqCitys = payload;
        },
        setLocalCity (state,payload) {
            state.localCity = payload;
        }
    },
    actions: {
        async asyFetchAllCity(ctx) {
            if (ctx.state.isGet) {
                return ctx.state.allCity;
            }
            var res = await api.fetchCityInfo();
            ctx.commit("setAllCity", res.data);
            return res;
        },
        async asyFetchHotCity({commit}) {
            var res = await api.fetchHotCity();
            commit("setHotCitys",res.data);
            return res;
        },
        async asyFetchReqCitys ({commit}) {
            var res = await api.fetchReqCity();
            commit("setReqCitys",res.data);
            return res;
        }

    },
    getters: {
        computedProvince(state) {
            // 统计省份
            return state.allCity.map(item => item.provinceName);
        },
        computedCity(state) {
            // 省份的所有城市
            var allCity = state.allCity;
            for (let i = 0; i < allCity.length; i++) {
                if(allCity[i].provinceName === state.selProvince) {
                    return allCity[i];
                }
            }
            return {cityInfoList : []};
        }
    }
}