<template>
  <div
    id="app"
    :class="{
      'bg-mt': useBgMt,
    }"
  >
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  data() {
    return {
      useBgMt: true,
    };
  },
  watch: {
    "$route.fullPath"(newValue) {
      if (newValue.includes("login") || newValue.includes("register")) {
        this.useBgMt = false;
      } else {
        this.useBgMt = true;
      }
     

    },
  },
};
</script>

<style lang="less" scoped>
.bg-mt {
  background-color: #f8f8f8;
}
</style>