
export const routes = [
    {
        path: '/',
        redirect: "/index/home"
    },
    {
        path: "/index",
        name: 'Index',
        component: () => import("@/views/Index"),
        children : [
            {path : 'home',name:"Home",component : ()=>import ("@/views/Index/HomeIndex")},
            {path : "city",name : "City",component : ()=>import ("@/views/Index/City")},
            {path : 's',name:"Result",component : ()=>import("@/views/Index/Result")},
            {path : 'meishi',name:"Detail",component : ()=>import("@/views/Index/Detail")}
        ]
    },
    {
        path : "/index.html",
        redirect : "/"
    },
    {
        path: '/login',
        name: 'Login',
        component: () => import('@/views/Login') // 异步组件
    },{
        path : '/register',
        name : 'Register',
        component : ()=>import('@/views/Register')
    }
]