import api from "@/api";
export default {
    state() {
        return {
            userName: '',
        }
    },
    mutations: {
        setUserName(state, payload) {
            state.userName = payload;
        }

    },
    actions: {
        async asyPostLogin({ commit }, payload) {
            var res = await api.postLogin(payload);
            if (res.status == 'success') {
                // 将账号同步到仓库中，进行联动
                let { userName } = payload;
                commit("setUserName", userName);
            } 
            return {
                status : res.status,
                msg : res.msg // 这个信息需要渲染到提示框里
            }
        }

    },
    namespaced : true
    // [vuex] unknown action type: userStore/asyPostLogin
    // 报错原因没有开启命名空间
}